import React from 'react';

import HzTable from '../../HzTable';
import UserWidget from '../../widgets/UserWidget';
import MoneyWidget from '../../widgets/MoneyWidget';
import DateWidget from '../../widgets/DateWidget';

import fetch2 from 'whatwg-fetch';
console.log("fetch", fetch);

export default class UsCongress extends React.Component {
  constructor(props) {
    super(props);
    this.state = {loading: true};
  }
  
  componentDidMount() {
    fetch("https://www.govtrack.us/api/v2/role?current=true&limit=600")
    .then(function(response) {
      return response.json();
    })
    .then(data => {
      console.log(data);
      this.rows = [];

      data.objects.forEach(obj => {
        const row = {
          name: {
            first: obj.person.firstname,
            last: obj.person.lastname,
          },
          party: obj.party,
          gender: obj.person.gender,
          role: obj.role_type_label,
          state: obj.state,
          endDate: obj.enddate,
          twitterId: obj.person.twitterid,
          website: obj.website,
        };

        this.rows.push(row);
      });

      this.columns = [
        {
          key: "name",
          displayName: "Name",
          defaultSort: "asc",
          widget: "name",
        },
        {
          key: "state",
          displayName: "State",
        },
        {
          key: "party",
          displayName: "Party",
        },
        {
          key: "gender",
          displayName: "Gender",
        },
        {
          key: "role",
          displayName: "Role",
        },
        {
          key: "endDate",
          displayName: "Term Ends",
        },
        {
          key: "twitterId",
          displayName: "Twitter",
          widget: "twitter",
        },
        {
          key: "website",
          displayName: "Website",
          widget: "website",
        },
      ];

      this.widgets = {
        name: {
          parser: function(name) {
            return name.first + " " + name.last;
          },
          
          renderer: function(name, parsedName) {
            return <td>{parsedName}</td>;
          },

          sortBy: function(name) {
            return [name.last, name.first];
          },
        },
        twitter: {
          renderer: function(twitterId) {
            if (!twitterId || twitterId === "") {
              return <td></td>;
            } else {
              return <td>@<a href={`http://twitter.com/${twitterId}`} target="_blank">{twitterId}</a></td>;
              }
          },
        },
        website: {
          renderer: function(url) {
            if (!url || url === "") {
              return <td></td>;
            } else {
              return <td><a href={url} target="_blank">{url}</a></td>;
              }
          },
        },
      };

      this.setState({loading: false});

    });
  }

  renderTable() {
    if (this.state.loading) {
      return <i>Loading...</i>;
    } else {
      return (
        <HzTable
        rows={this.rows}
        columns={this.columns}
        widgets={this.widgets}
        className="pure-table"
        />
      );
    }
  }
  
  render() {
    return (
      <div>
        <h2>United States Congress</h2>
        <div>
          {this.renderTable()}
        </div>
      </div>
    );
  }
}



